/**
 * 
 */

let windowWidth = 0
let windowHeight = 0
wx.getSystemInfo({
  success(res) {
    windowWidth = res.windowWidth
    windowHeight = res.windowHeight
  }
})

const getSystemInfo = () => {
  return [ windowWidth, windowHeight ]
}

const getUniqueKey = (() => {
  // 1ms
  let num = 0
  return function() {
    return new Date().getTime() + num++
  }
})();

const bindInstance = () => {
  let instance = {}

  return {
    getInstance: () => {
      return instance
    },
    /**
     * 
     */
    set: (bindName, data = null) => {
      if (!instance[bindName]) {
        instance[bindName] = data 
      }
      return instance[bindName]
    },
    get: (bindName) => {
      return instance[bindName] || null
    },
    /**
     * 
     */
    clear: () => {
      instance = {}
    },
    /**
     * 
     */
    remove: (bindName) => {
      instance[bindName] && delete instance[bindName]
    },
    /**
     * 
     * @param {*} key 
     */
    removeAllByKey: (key) => {
      if (!instance[key]) return
      // 
      for (let childKey of instance[key]) {
        instance[childKey] && delete instance[childKey]
      }
      // 
      delete instance[key]
    }
  }
};

module.exports = {
  getSystemInfo,
  getUniqueKey,
  cacheInstance: bindInstance(),
}
